'use client';

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Navigation, Autoplay } from "swiper/modules";
import { motion } from "framer-motion";

const slides = [
  {
    title: "Seamless Ship Freight for Global Trade",
    desc: "We partner with leading international shipping lines to move your cargo across major trade routes.",
    img: "/hero/bg1.jpg",
  },
  {
    title: "Fast Global Shipping You Can Trust",
    desc: "From full containers to consolidated loads, we provide dependable schedules worldwide.",
    img: "/hero/bg2.jpg",
  },
  {
    title: "Reliable Cargo Handling & Cost Savings",
    desc: "Optimized freight solutions with end-to-end tracking and transparency.",
    img: "/hero/bg3.jpg",
  },
];

export default function HeroSlider() {
  return (
    <section className="relative w-full h-[90vh]">
      {/* Background Slider */}
      <Swiper
        navigation
        autoplay={{ delay: 3000 }}
        modules={[Navigation, Autoplay]}
        className="h-full w-full"
      >
        {slides.map((slide, index) => (
          <SwiperSlide key={index}>
            <div
              className="h-full w-full bg-cover bg-center"
              style={{ backgroundImage: `url(${slide.img})` }}
            >
              <div className="bg-black/50 h-full w-full flex items-center">
                {/* Left Content */}
                <motion.div
                  initial={{ opacity: 0, x: -40 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.6 }}
                  className="text-white max-w-xl pl-10 md:pl-20"
                >
                  <h1 className="text-4xl md:text-6xl font-bold leading-tight mb-4">
                    {slide.title}
                  </h1>
                  <p className="text-lg md:text-xl mb-6">{slide.desc}</p>
                  <button className="bg-blue-600 px-6 py-3 rounded-full font-semibold">
                    Book Shipment
                  </button>
                </motion.div>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>

      {/* Right Booking Form */}
      <div className="absolute right-6 top-1/2 -translate-y-1/2 bg-white shadow-xl p-6 rounded-2xl w-[330px]">
        <h3 className="text-lg font-semibold mb-4">
          Your Booking is a Step Away!
        </h3>

        <input
          type="text"
          placeholder="Enter your pickup location"
          className="w-full border p-3 rounded-lg mb-3"
        />

        <input
          type="text"
          placeholder="Enter your dropping location"
          className="w-full border p-3 rounded-lg mb-4"
        />

        <button className="w-full bg-blue-600 text-white py-3 rounded-xl">
          Book Now
        </button>
      </div>
    </section>
  );
}
